import React, { Component, } from "react";
import Slider from "react-slick";
import certFE from "../sources/html_css_js.jpg";
import certJS from "../sources/JS.jpg";
import vid1 from "../sources/go-surf.mp4";
import vid2 from "../sources/belcop.mp4";
import vid3 from "../sources/layout.mp4";
import vid4 from "../sources/texode.mp4";

export default class SimpleSlider extends Component {
  constructor(props) {
    super(props);
    this.state = { isModal: false, video: "", cardId: 0 };
    // This binding is necessary to make `this` work in the callback
    this.handleClick = this.handleClick.bind(this);
  }
  handleClick(e) {
    this.setState((prevState) => ({
      isModal: true,
      video: e.target.src,
      cardId: e.target.id,
    }));
  }

  render() {
    const settings = {
      dots: false,
      fade: false,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
    };
    const video = [
      { src: vid1, git: 'www.google.com' },
      { src: vid2, git: 'https://github.com/Ilya-Nahorny/BelCopGame' },
      { src: vid3, git: 'www.google.com' },
      { src: vid4, git: 'www.google.com' },
    ];
    return (
      <div className="slider-case">


        <Slider {...settings}>
          <div className="slide">
            <h3 className="title">{this.props.case.certificates.title}</h3>
            <div className="certificates">
              <div className="cert cert-FE">
                <a href={certFE}><img src={certFE}></img></a>
                <p>
                  {this.props.case.certificates.descrFE}
                  <br />
                  <span>
                    {this.props.case.certificates.school}
                    <br />
                    (04.2019 - 7.2019)
                  </span>
                </p>
              </div>
              <div className="cert cert-JS">
                <a href={certJS}><img src={certJS}></img></a>
                <p>
                  {this.props.case.certificates.descrJS}
                  <br />
                  <span>
                    {this.props.case.certificates.school}
                    <br />
                    (08.2019 - 11.2019)
                  </span>
                </p>
              </div>
            </div>
          </div>
          <div className="slide">
            <h3 className="title">{this.props.case.exp.title}</h3>
            <div className="examples">
              {video.map((arr, ind) => {
                return (
                  <a className="card" onClick={this.handleClick} href={video[ind].git} alt={video[ind].git}>
                    <span className="card_link" >git</span>
                    <video
                      src={video[ind].src}
                      width={500}
                      height={300}
                      muted
                      autoPlay
                      loop
                    ></video>
                  </a>
                );
              })}
            </div>
          </div>

        </Slider>
      </div>
    );
  }
}
