import React from "react";
import { Route, Routes } from "react-router-dom";
import { Link } from "react-router-dom";
import "./App.scss";
import "./components/scss/import.scss";
import IconHome from "./components/js/IconHome";
import IconProfile from "./components/js/IconProfile";
import IconDownload from "./components/js/IconDownload";
import IconCase from "./components/js/IconCase";
import IconContact from "./components/js/IconContact";
import Home from "./components/js/home";
import Profile from "./components/js/Profile";
import Contact from "./components/js/Contact";
import Case from "./components/js/Case";
import { Button } from "./components/js/button";

const en = {
  label: "en",
  homePageH1: "Hey, my name is Ilya.",
  profile: {
    title: "Profile.",
    about: [
      "I am a web and frontend developer. Studied web application development from the very basics. successfully completed various courses in the High-Tech Park of Belarus, worked on freelance websites.",
      " ++ I am well-organized and motivated person with a high lev of discipline.",
      " ++ Big lover of break dance, travelling,good music and positive people.",
    ],
  },
  case: {
    certificates: {
      title: "Certificates.",
      descrFE: "Website development using HTML, CSS and JavaScript.",
      descrJS: "JavaScript web application development.",
      school: "COURSES EDUCATIONAL CENTER FOR PROGRAMMING AND HIGH TECH.",
    },
    exp: {
      title: "PET projects.",
    },
    petProjects: "",
  },
  contact: {
    title: "Сontact.",
  },
};
const pl = {
  label: "pl",
  homePageH1: "Hej, mam na imie Ilya.",
  profile: {
    title: "Profil.",
    about: [
      "I am a web and frontend developer. Studied web application development from the very basics. successfully completed various courses in the High-Tech Park of Belarus, worked on freelance websites.",
      " ++ I am well-organized and motivated person with a high lev of discipline.",
      " ++ Big lover of break dance, travelling,good music and positive people.",
    ],
  },
  case: {
    certificates: {
      title: "Certyfikaty.",
      descrFE:
        "Tworzenie stron internetowych z wykorzystaniem HTML, CSS i JavaScript.",
      descrJS: "Tworzenie aplikacji internetowych JavaScript.",
      school: "KURSY CENTRUM EDUKACYJNE PROGRAMOWANIA I HIGH TECH.",
    },
    exp: {
      title: "PET Projekty.",
    },
    petProjects: "",
  },
  contact: {
    title: "Kontakt.",
  },
};

const App = () => {
  const navButtons = [
    <Link 
      to="/cv"
      className="link"
      onClick={() => {
        if (menuVisibility) {
          handlerMenu();
        }
      }}
    >
      <IconHome />
    </Link>,
    <Link
      to="/Profile"
      className="link"
      onClick={() => {
        if (menuVisibility) {
          handlerMenu();
        }
      }}
    >
      <IconProfile />
    </Link>,
    <Link
      to="/Case"
      className="link"
      onClick={() => {
        if (menuVisibility) {
          handlerMenu();
        }
      }}
    >
      <IconCase />
    </Link>,
    <Link
      to="/Contact"
      className="link"
      onClick={() => {
        if (menuVisibility) {
          handlerMenu();
        }
      }}
    >
      <IconContact />
    </Link>,
    <Link
      to="/Case"
      className="link"
      onClick={() => {
        if (menuVisibility) {
          handlerMenu();
        }
      }}
    >
      <IconDownload />
    </Link>,
  ];
  const [translate, setTranslate] = React.useState(en);

  const handlerLanguage = () => {
    if (translate !== pl) {
      setTranslate(pl);
    }
    if (translate !== en) {
      setTranslate(en);
    }
  };

  const [menuVisibility, setMenuVisibility] = React.useState(false);

  const handlerMenu = () => {
    if (menuVisibility === false) {
      setMenuVisibility(true);
    }
    if (menuVisibility === true) {
      setMenuVisibility(false);
    }
  };

  return (
    <div className="App">
      <div className="languageToggle" id="languageToggle" onClick={handlerLanguage}>
        {translate.label}
      </div>
      <div className="menuToggle" id="menuToggle" onClick={handlerMenu}>
        <i>...</i>
      </div>
      <div className={menuVisibility ? "navigation-active" : "navigation"}>
        {navButtons.map((arr, ind) => {
          return arr;
        })}
      </div>

      <Routes>
        <Route path="/cv" element={<Home {...translate} />} exact />
        <Route path="/Profile" element={<Profile {...translate} />} exact />
        <Route path="/Contact" element={<Contact {...translate} />} exact />
        <Route path="/Case" element={<Case {...translate} />} exact />
            <Route
      path="*"
      element={
        <main style={{ padding: "1rem" }}>
          <p>There's nothing here!</p>
        </main>
      }
    />
      </Routes>
    </div>
  );
};

export default App;
