import axios from "axios";
import React from "react";
import MailSvg from "./MailSvg";


export default function Contact(props) {
  let nameInp = document.querySelector("#name-input");
  let emailInp = document.querySelector("#email-input");
  let textarea = document.querySelector("#textarea");
  let sendBtn = document.querySelector("#send-btn");

  let valid = function (e) {
    // e.preventDefault()
    let feedback = "Name: " + nameInp.value + " Email: " + emailInp.value + " Message: " +  textarea.value;
    fetch(
      "https://api.telegram.org/bot5243359912:AAEAO7IMaJZQw5XnRqc7wEsKx742ky5ewdI/sendMessage?chat_id=-1001598250417&parse_mode=html&text=" +
        feedback,
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json;charset=utf-8",
        },
        body: JSON.stringify(),
      }
    ).then(alert("Message sent / Wiadomośc została wysłana"))
    .catch(alert("Form error / Błęd formy"))
  
  };
  

  const [userName, setUserName] = React.useState('');
  const [email, setEmail] = React.useState('');
  const [emailDirty, setEmailDirty] = React.useState(false);
  const [emailError, setEmailError] = React.useState('fill your email');
  const [message, setMessage] = React.useState('');
  const [formValid, setFormValid] = React.useState(false);

  React.useEffect(() => {
    if(emailError){
      setFormValid(false)
    } else {
      setFormValid(true)
    }
  }, [emailError]);

  const emailHadler = (e) => {
    setEmail(e.target.value)
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/

    if(!re.test(String(e.target.value).toLowerCase())){
      setEmailError('uncorrect email')
    } else{
      setEmailError('')
    }
  }

  const blur = (e) => {
    
    if( e.target.name === "user_email" ) {
      setEmailDirty(true)
    }
  }

  return (
    <div className="contact">
      <h1 className="title">{props.contact.title}</h1>
      <div className="contact_form">
      <form action="" method="">
          <input onChange={(e) => {setUserName(e.target.value)}} value={userName} type="text" name="user_name" id="name-input"  placeholder="name / imie"/>
          {(emailDirty && emailError) && <div className='error-email'>{emailError}</div>}
          <input onBlur={e => blur(e)}
            onChange={ (e) => {emailHadler(e);setEmail(e.target.value)}}
            value={email}
            type="text"
            name="user_email"
            id="email-input"
            placeholder="email / poczta"
          />
          <textarea
          placeholder="...mesage / wiadomośc"
            onChange={(e) => {setMessage(e.target.value)}}
            name="user_message"
            id="textarea"
            cols=""
            rows=""
          ></textarea>
        <button disabled={!formValid} type="button" id="send-btn" onClick={valid}>
          Send
        </button>
      </form>
      </div>
      <MailSvg/>
    </div>
  );
}
